.letter {
  font-size: 5em;
  width: 1.5em;
  height: 1.5em;
  border-radius: 0.2em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  color: #5a6174;
  font-weight: bold;
  background-color: #76c3bd;
  -webkit-transition: color 0.5s ease;
  -o-transition: color 0.5s ease;
  transition: color 0.5s ease;
}

@-webkit-keyframes newLetterAnimation {
  0% {
    background-color: #609bcc;
    color: #609bcc;
  }
  50% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  100% {
    background-color: #76c3bd;
    color: #5a6174;
  }
}

@keyframes newLetterAnimation {
  0% {
    background-color: #609bcc;
    color: #609bcc;
  }
  50% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  100% {
    background-color: #76c3bd;
    color: #5a6174;
  }
}

.placeholder {
  background-color: #609bcc;
}

.letter:not(.placeholder) {
  -webkit-animation-name: newLetterAnimation;
          animation-name: newLetterAnimation;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}



